import { useCallback } from 'react';
import { Controller, ControllerRenderProps } from 'react-hook-form';

import { ColorInputPicker, ColorPickerProps } from '@wp-plugins/adapters';

export const ColorPicker: React.FC<ColorPickerProps & { name: string }> = (props) => {
	const render = useCallback(
		({ onChange, value }: ControllerRenderProps) => (
			<ColorInputPicker {...props} onChange={onChange} color={value} />
		),
		[props]
	);
	return <Controller name={props.name} render={render} />;
};
